export function getParentForHighlight(node) {
  var parent = node.parentNode;

  if (parent instanceof HTMLElement) {
    var computedStyle = window.getComputedStyle(parent);
    var position = computedStyle.getPropertyValue('position');
    var overflow = computedStyle.getPropertyValue('overflow');
    var metadata = {
      hasRelativePosition: position === 'relative',
      hasRestrictedOverflow: overflow.split(' ').some(function (value) {
        return ['auto', 'scroll', 'clip', 'hidden'].includes(value);
      })
    };
    return metadata.hasRelativePosition || metadata.hasRestrictedOverflow ? [parent, metadata] : getParentForHighlight(parent);
  }

  return [null, null];
}
export function getRelativeScrollOffset(node) {
  if (!node || !(node instanceof HTMLElement)) {
    return [0, 0];
  }

  var offsetParent = node.offsetParent;
  var scrollTop = 0;
  var scrollLeft = 0;
  var currentNode = node;

  while (currentNode.parentElement) {
    currentNode = currentNode.parentElement;

    if (offsetParent && offsetParent.isSameNode(currentNode)) {
      break;
    }

    scrollTop += currentNode.scrollTop;
    scrollLeft += currentNode.scrollLeft;
  }

  return [scrollTop, scrollLeft];
}